import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { CommonService } from 'src/app/core/services/common.service';
import { PartyService } from 'src/app/core/services/party.service';
import { SignupService } from 'src/app/core/services/signup.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { WarehouseService } from 'src/app/core/services/warehouse.service';
import { debounceTime, map, startWith } from "rxjs/operators";
import { RolesPermissionsService } from 'src/app/core/services/roles-permissions.service';

@Component({
  selector: 'app-add-warehouse',
  templateUrl: './add-warehouse.component.html',
  styleUrls: ['./add-warehouse.component.scss']
})
export class AddWarehouseComponent implements OnInit {

  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  public warehouseForm: any;
  public submitted: boolean = false;
  public today: Date;
  countryList: any[];
  private pincodeSubscription: Subscription;
  pinDetails: any;


  constructor(private formBuilder: FormBuilder,public rolePermissionservice: RolesPermissionsService,
    public router: Router, private commonService: CommonService,
    private toastService: ToastNotificationService, public dialog: MatDialog,
    private titleCasePipe: TitleCasePipe,
    // @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<AddWarehouseComponent>,
    private warehouseService: WarehouseService,
    public signupService: SignupService,
    private partyService: PartyService
  ) {
    this.today = new Date();
  }

  ngOnInit(): void {
    try {
      this.warehouseForm = this.formBuilder.group({
          name: [null, Validators.required],
          shortName: [null, Validators.required],
          businessName: [null, Validators.required],
          address: [null, Validators.required],
          email: [null, Validators.required, Validators.pattern("^[a-zA-Z0-9+_.-]+@[a-zA-Z0-9.-]+$")],
          password: [null, Validators.required],
          country: [102],
          pincode: [null],
          city: [null],
          state: [null],
          mobile: [null],
          contactPerson: [null],
      });
      this.warehouseForm.get('pincode').valueChanges
    .pipe(debounceTime(500)) // Add a debounce time to avoid making API calls on every keystroke
    .subscribe((pincode) => {
      this.getPincodeDetails(pincode);
    });
  

      this.getAllCountries()
      
    } catch (error) {
        this.toastService.toastMsg({
          title: 'Error',
          content: error
        })
    }
  }  
  
  get f() {
    return this.warehouseForm.controls;
  }
  
  getPincodeDetails(pin) {
    this.rolePermissionservice.getEmployeePincode({}, pin).then((res) => {
      this.pinDetails = res;
      this.warehouseForm.patchValue({
        state: res.data.stateName,
        city: res.data.districtName
      });
    });
  }
  cancel() {
    this.dialogRef.close();
  }

  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  numberDecimalOnly(event): boolean {
    var charCode = (event.which) ? event.which : event.keyCode;
    if (charCode != 46 && charCode > 31
      && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;
  }

  onSave() {
    this.submitted = true;
    console.log(this.warehouseForm.value.country)
    if (this.warehouseForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      return false;
    } else {
      // const data = {
      //     // "id": "4", //optional
      //     "userId": this.currentUser.id,
      //     "name": this.warehouseForm.value.name,
      //     "shortName": this.warehouseForm.value.shortName,
      //     "companyName":this.warehouseForm.value.businessName,
      //     "address": this.warehouseForm.value.address,
      //     "pincode": this.warehouseForm.value.pincode,
      //     "city": this.warehouseForm.value.city,
      //     "state": this.warehouseForm.value.state,
      //     "isActive":true,
      //     "countryId": this.warehouseForm.value.country,
      //     "contactPerson": this.warehouseForm.value.contactPerson,
      //     "mobile": this.warehouseForm.value.mobile
      // }
      const data = {
          // "id": "4", //optional
          email: this.warehouseForm.value.email,
          password: this.warehouseForm.value.password,
          countryId: "102",
          firstName: this.warehouseForm.value.name,
          lastName: this.warehouseForm.value.shortName,
          parentId: this.currentUser.id
      }
      this.signupService.warehouseRegister(data).then( res => {
      // this.warehouseService.postWarehouse(data).then( res => {
        if(res.success) {
          this.dialogRef.close();
          this.commonService.notifyDataAdded()
          this.toastService.toastMsg({
            title: "Success",
            content: "Warehouse Addedd Successfully!!!",
          });
        }
      })
    }
  }

  getAllCountries() {
    this.partyService.getAllCountries({}).then((res) => {
      this.countryList = res.data;
    });
  }
}
